.additional-product__wrapper {
  display: flex;
  padding-top: 60px;
  padding-bottom: 60px;
}

.additional-product__wrapper h2 {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 0;
  margin-right: 10px;

  font-family: $syncopate-font;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 50px;
  text-transform: uppercase;

  color: #21201f;
}

.additional-product__link {
  display: flex;

  transition: 0.5s;
  outline: 0;

  &:hover {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 5px;
  }

  &:focus,
  &:active {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 5px;
  }
}

.additional-product__image-wrapper {
  display: flex;
}

.additional-product__image {
  display: block;
}

@media (max-width: $tablet-width) {
  .additional-product__wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .additional-product__wrapper h2 {
    font-size: 28px;
    line-height: 32px;
  }
}

@media (max-width: $mobile-width) {
  .additional-product__wrapper {
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .additional-product__image-wrapper {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .additional-product__wrapper h2 {
    margin-right: 0;
    order: 2;

    font-size: 36px;
    line-height: 42px;
  }
}

