.page-header__wrapper {
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
}

.page-header__nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 70px;
}

.page-header__toggle {
  display: none;
}

.page-header__nav {
  flex: 1;
}

.page-header__list {
  @include reset-list;

  display: flex;
  max-height: 70px;

  &--user-nav {
    flex: 1;
    align-items: center;
    justify-content: flex-end;
  }
}

.page-header__link {
  display: block;
  padding: 14px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #21201f;

  transition: ease-in 0.5s;
  outline: 0;

  &:hover {
    opacity: 0.7;
  }

  &:focus,
  &:active {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 5px;
  }
}

.page-header__logo {
  flex: 1;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;

  outline: 0;
  transition: ease-in 0.5s;

  &:hover {
    opacity: 0.8;
  }

  &:focus,
  &:active {
    .page-header__icon--logo {
      fill: #8db2d0;
    }
  }
}

.page-header__icon {
  vertical-align: middle;

  fill: #21201f;
}

.page-header__logo--image {
  width: 305px;
  height: 30px;
}

.page-header__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  color: #21201f;

  background-color: transparent;
  border: none;

  cursor: pointer;
  transition: ease-in 0.5s;
  outline: 0;

  &:hover {
    opacity: 0.7;
  }

  &:focus,
  &:active {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 5px;
  }
}

.page-header__icon--search {
  width: 16px;
  height: 16px;
}

.page-header__form {
  display: none;
  justify-content: space-between;
  align-items: center;

  // border-bottom: 1px solid white;
}

.page-header__form input {
  width: 100%;
  padding: 0;
  padding-left: 55px;
  min-height: 48px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 32px;
  color: #21201f;

  border: none;
  outline: 0;

  background-color: transparent;

  &:focus,
  &:active {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 30px;
  }

  &::placeholder {
    color: #9b9997;
  }
}

.page-header__button--search-form {
  position: absolute;
  display: none;
}

.page-header__form label {
  width: 100%;
}

.page-header__search-text {
  display: inline-block;
  padding-left: 5px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  color: #21201f;
}

.page-header__icon--close {
  width: 20px;
  height: 20px;
}

.page-header__icon--cart {
  display: none;
}

.page-header__icon--heart {
  width: 24px;
  height: 20px;
}

.page-header--active {
  .page-header__form {
    position: relative;
    display: flex;
  }

  .page-header__button--close-form {
    position: absolute;
    right: 10px;

    display: block;
    padding: 10px;

    transition: none;

    &:hover {
      opacity: 1;
      background-color: #8db2d0;
      border-radius: 50%;
      box-shadow: none;
    }

    &:focus,
    &:active {
      background-color: #8db2d0;
      border-radius: 50%;
      box-shadow: none;
    }
  }

  .page-header__button--search-form {
    position: absolute;
    left: 10px;

    display: block;
    padding: 10px;

    transition: none;

    &:hover {
      opacity: 1;
      background-color: #8db2d0;
      border-radius: 50%;
      box-shadow: none;
    }

    &:focus,
    &:active {
      background-color: #8db2d0;
      border-radius: 50%;
      box-shadow: none;
    }
  }
}

.page-header__list--subnav {
  display: none;
}

.page-header__button--close-menu {
  position: absolute;
  display: none;
}

@media (max-width: $tablet-width) {
  .page-header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }

  .page-header__wrapper {
    background-color: #ffffff;
  }

  .page-header__toggle {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 14px;

    border: none;
    cursor: pointer;
    background-color: transparent;
    transition: ease-in 0.5s;
    outline: 0;

    &:hover {
      opacity: 0.7;
    }

    &:focus,
    &:active {
      box-shadow: 0 0 5px 2px #8db2d0;
      border-radius: 5px;
    }
  }

  .page-header__list--main-nav {
    display: none;

    font-family: "Arial", "Verdana", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }

  .page-header__icon--menu {
    width: 40px;
    height: 28px;
  }

  .page-header__item {
    margin-left: 10px;
    margin-right: 10px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .page-header__icon--logo {
    width: 285px;
  }

  .page-header__list--user-nav {
    .page-header__item:last-child {
      padding-right: 0;
    }
  }

  .page-header__icon--search {
    width: 32px;
    height: 32px;
  }

  .page-header__item--login {
    display: none;
  }

  .page-header__search-text {
    display: none;
  }

  .page-header__bag {
    display: none;
  }

  .page-header__icon--cart {
    display: block;
  }

  .page-header__icon--heart {
    width: 36px;
    height: 32px;
  }

  .page-header__icon--close {
    width: 23px;
    height: 23px;
  }

  .page-header--active {
    .page-header__form {
      margin-left: 0;
    }
  }

  .page-header__form input {
    padding-left: 60px;

    font-size: 20px;
    line-height: 36px;
  }

  .page-header__button--search-form {
    .page-header__icon--search {
      width: 23px;
      height: 23px;
    }
  }


}

@media (max-width: $mobile-width) {
  .page-header__wrapper {
    padding-left: 2%;
    padding-right: 2%;
  }

  .page-header__item {
    margin: 0;
  }

  .page-header__icon--logo {
    width: 150px;
  }

  .page-header__icon--menu {
    width: 28px;
    height: 20px;
  }

  .page-header__icon--search {
    width: 25px;
    height: 25px;
  }

  .page-header__icon--cart {
    width: 21px;
    height: 25px;
  }

  .page-header__icon--heart {
    width: 28px;
    height: 25px;
  }

  .page-header__form input {
    font-size: 14px;
    line-height: 28px;
  }

  .page-header__mobile-menu--active {
    width: 80%;
    padding: 15px;

    .page-header__button--close-menu {
      top: 28px;
      right: 20px;
    }
  }

  .page-header__form input {
    min-height: 40px;

    font-size: 16px;
    line-height: 30px;
    background-color: #ffffff;
    border-radius: 30px;
  }

  .page-header--active {
    .page-header__button--close-form {
      right: 5px;
      padding: 6px;

      background-color: #8db2d0;
      border-radius: 50%;
    }

    .page-header__button--search-form {
      left: 5px;
      padding: 6px;

      background-color: #8db2d0;
      border-radius: 50%;
    }
  }
}

@media (max-width: 400px) {
  .page-header__toggle {
    padding: 5px;
  }

  .page-header__button {
    padding: 5px;
  }

  .page-header__link {
    padding: 5px;
  }

  .page-header__nav-wrapper {
    justify-content: space-between;
  }

  .page-header__nav {
    flex: none;
  }

  .page-header__logo {
    flex: none;
  }

  .page-header__list--user-nav {
    flex: none;
  }
}
