.catalog__list {
  @include reset-list;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.catalog__description h3 {
  margin: 0;
  margin-bottom: 5px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #21201f;
}

.catalog__item-price {
  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
  text-transform: uppercase;
  color: #21201f;
}

.catalog__item {
  margin-left: 1%;
  margin-right: 1%;
  margin-top: 1%;
  margin-bottom: 1%;
}

.catalog__icon--heart {
  fill: #ffffff;
  display: none;
}

.catalog__link {
  position: relative;

  transition: 0.5s;
  outline: 0;

  &:hover {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 5px;

    .catalog__icon--heart {
      position: absolute;
      bottom: 20px;
      left: 20px;

      display: flex;
    }
  }

  &:focus,
  &:active {
    box-shadow: 0 0 5px 2px #8db2d0;
    border-radius: 5px;

    .catalog__icon--heart {
      position: absolute;
      bottom: 20px;
      left: 20px;

      display: flex;
    }
  }
}

.catalog__item-inner {
  display: flex;
}

.catalog__link img {
  display: block;
}

.catalog__description {
  display: none;
}

@media (max-width: $tablet-width) {
  .catalog__item {
    display: flex;
    flex-direction: column;
    flex-basis: 48%;
    justify-content: center;
    align-items: center;
    margin-top: 2%;
    margin-bottom: 2%;
  }

  .catalog__item-inner {
    display: flex;
    flex-direction: column;
  }

  .catalog__link {
    position: relative;

    margin-bottom: 20px;


    &:hover {
      box-shadow: 0 0 5px 2px #8db2d0;
      border-radius: 5px;
    }

    &:focus,
    &:active {
      box-shadow: 0 0 5px 2px #8db2d0;
      border-radius: 5px;
    }
  }

  .catalog__icon--heart {
    position: absolute;
    bottom: 20px;
    left: 20px;

    display: block;
    width: 37px;
    height: 32px;
  }

  .catalog__description {
    display: block;
  }

