@media (max-width: $mobile-width) {
  .catalog__item {
    flex-basis: auto;
    margin-left: 0;
    margin-right: 0;
  }

  .catalog__link {
    margin-bottom: 15px;
  }

  .catalog__icon--heart {
    bottom: 10px;
    left: 10px;
  }

  .catalog__description h3 {
    font-size: 14px;
    line-height: 18px;
  }

  .catalog__item-price {
    font-size: 14px;
    line-height: 18px;
  }
}
