.promo__wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
}

.promo__wrapper h2 {
  margin: 0;
  margin-bottom: 30px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 48px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  text-align: center;
}

.promo__list {
  @include reset-list;
  display: flex;
  justify-content: center;
  align-items: center;
}

.promo__link {
  display: block;

  transition: 0.5s;
  outline: 0;

  &:hover {
    box-shadow: 0 0 10px 5px #8db2d0;
    border-radius: 5px;
  }

  &:focus,
  &:active {
    box-shadow: 0 0 10px 5px #8db2d0;
    border-radius: 5px;
  }
}

.promo__image {
  display: block;
}

@media (max-width: $tablet-width) {
  .promo__wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .promo__wrapper h2 {
    font-size: 26px;
    line-height: 32px;
  }

  .promo__list {
    flex-wrap: wrap;
    margin-left: 5%;
    margin-right: 5%;
  }
}

@media (max-width: $mobile-width) {
  .promo__wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .promo__wrapper h2 {
    font-size: 20px;
    line-height: 32px;
  }

  .promo__list {
    margin: 0;
  }
}
